import { Text, Badge, IndexTable, LegacyCard, useIndexResourceState } from '@shopify/polaris';
import React from 'react';

import './tablet.css';

// type PaymentStatus = {
//   status: 'success-strong' | '',
//   title: 'Paid' | 'Not Paid',
// }

interface Orders {
  id: string,
  order: string,
  date: string,
  paymentStatus: boolean,
  fulfillment: boolean,
  trackingNumber: string,
  DPDStatus: string,
  customer: string,
  total: string,
}

export const Tablet: React.FC = () => {
  const orders = [
    {
      id: '1020',
      order: '#1020',
      date: 'Jul 20 at 4:34pm',
      paymentStatus: false,
      fulfillment: false,
      trackingNumber: '234554',
      DPDStatus: '',
      customer: 'Jaydon Stanton',
      total: '$969.44',
    },
    {
      id: '1019',
      order: '#1019',
      date: 'Jul 20 at 3:46pm',
      paymentStatus: true,
      fulfillment: false,
      trackingNumber: '',
      DPDStatus: '',
      customer: 'Ruben Westerfelt',
      total: '$701.19',
    },
    {
      id: '1018',
      order: '#1018',
      date: 'Jul 20 at 3.44pm',
      paymentStatus: true,
      fulfillment: true,
      trackingNumber: '234554',
      DPDStatus: '',
      customer: 'Leo Carder',
      total: '$798.24',
    },
  ];
  const resourceName = {
    singular: 'order',
    plural: 'orders',
  };

  const { selectedResources, allResourcesSelected, handleSelectionChange } =
    useIndexResourceState(orders);

  const rowMarkup = orders.map(
    (
      { id, order, date, customer, total, paymentStatus, fulfillment, trackingNumber, DPDStatus },
      index,
    ) => (
      <IndexTable.Row
        id={id}
        key={id}
        selected={selectedResources.includes(id)}
        position={index}
      >
        <IndexTable.Cell>
          <Text variant="bodyMd" fontWeight="bold" as="span">
            {order}
          </Text>
        </IndexTable.Cell>
        <IndexTable.Cell>{date}</IndexTable.Cell>
        <IndexTable.Cell>
          {paymentStatus
            ? (
              <Badge tone='success-strong'>Paid</Badge>
            )
            : (
              <Badge>Not paid</Badge>
            )}
        </IndexTable.Cell>
        <IndexTable.Cell>
          {fulfillment
            ? (
              <Badge tone='success-strong'>fulfilled</Badge>
            )
            : (
              <Badge>unfulfilled</Badge>
            )}
        </IndexTable.Cell>
        <IndexTable.Cell>{trackingNumber === '' ? '-' : trackingNumber}</IndexTable.Cell>
        <IndexTable.Cell>{DPDStatus}</IndexTable.Cell>
        <IndexTable.Cell>{customer === '' ? '-' : customer}</IndexTable.Cell>
        <IndexTable.Cell>{total}</IndexTable.Cell>
      </IndexTable.Row>
    ),
  );

  return (
    <LegacyCard>
      <IndexTable
        resourceName={resourceName}
        itemCount={orders.length}
        selectedItemsCount={
          allResourcesSelected ? 'All' : selectedResources.length
        }
        onSelectionChange={handleSelectionChange}
        headings={[
          { title: 'Order' },
          { title: 'Date' },
          { title: 'Payment' },
          { title: 'Fulfillment' },
          { title: 'Tracking number' },
          { title: 'DPD Status *' },
          { title: 'Customer' },
          { title: 'Total' },
        ]}
      >
        {rowMarkup}
      </IndexTable>
    </LegacyCard>
  );
};
